  -moz-animation-delay: 1s;
  -ms-animation-delay: 1s;
  -o-animation-delay: 1s;
  animation-delay: 1s;
}

.btn {
  position: relative;
  display: block;
  color: white;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  text-decoration: none;
  margin: 30px 0;
  border: 2px solid #e3fffb;
  padding: 14px 59.8px;
  text-transform: uppercase;
  overflow: hidden;
  transition: 1s all ease;
}

.btn::before {
  background: #00735c;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.6s ease;
}

.btn3::before {
  width: 100%;
  height: 0%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.btn3:hover::before {
  height: 380%;
}

