}
.day2 .ico .line1, .day2 .ico .line2, .day2 .ico .line3 {
  position: absolute;
  width: 7.4rem;
  height: 0.7rem;
  border-radius: 3px;
  box-shadow: 0 0 6px rgba(26, 26, 26, 0.7);
  background-color: azure;
}
.day2 .ico .line1 {
  top: 1rem;
  left: 9.3rem;
  z-index: 3;
  animation: line1XR 1s both;
}
.day2 .ico .line2 {
  top: 3rem;
  left: 9.3rem;
  z-index: 1;
  transition: 1.2s;
}
.day2 .ico .line3 {
  top: 5rem;
  left: 9.3rem;
  z-index: 2;
  animation: line2XR 1s both;
}
.day2 .ico.active .line1 {
  animation: line1X 1s both;
}
.day2 .ico.active .line2 {
  width: 0rem;
  transform: translateX(3.8rem);
  transition: 0.5s;
}
.day2 .ico.active .line3 {
  animation: line2X 1s both;
}
@keyframes line1X {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: translateY(2rem) rotate(0);
  }
  100% {
    transform: translateY(2rem) rotate(135deg);
    background-color: brown;
  }
}
@keyframes line1XR {
  0% {
    transform: translateY(2rem) rotate(135deg);
  }
  50% {
    transform: translateY(2rem) rotate(0deg);
  }
  100% {
    transform: translateY(0) rotate(0deg);
  }
}
@keyframes line2X {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: translateY(-2rem) rotate(0);
  }
  100% {
    transform: translateY(-2rem) rotate(45deg);
    background-color: brown;
  }
}
@keyframes line2XR {
  0% {
    transform: translateY(-2rem) rotate(45deg);
  }
  50% {
    transform: translateY(-2rem) rotate(0);
  }
  100% {
    transform: translateY(0rem) rotate(0);
  }
