.day5 span {
  font-size: 1.4rem;
}
.day5 .calendar {
  box-shadow: 5px 5px 20px #5c5b5b;
  border-radius: 5px;
  overflow: hidden;
}
.day5 .calendar .infocalendar {
  background-color: #ebb758;
  display: flex;
  justify-content: space-between;
  align-content: center;
  margin: 0;
  padding: 1.5rem;
}
.day5 .calendar .infocalendar .money {
  text-align: end;
}
.day5 .calendar .grafics {
  background-color: azure;
  height: 19rem;
}
.day5 .calendar .grafics .index {
  display: flex;
  justify-content: flex-end;
}
.day5 .calendar .grafics .index span {
  font-family: "Roboto", sans-serif;
  font-weight: lighter;
  color: #202020;
  margin: 0;
  padding: 1.8rem;
  padding-top: 1rem;
  position: relative;
}
.day5 .calendar .grafics .index span::before {
  content: " ";
  position: absolute;
  left: -0.2rem;
  top: 1.2rem;
  display: block;
  width: 1.5rem;
  border-radius: 20px;
  height: 0.5rem;
}
.day5 .calendar .grafics .index span:nth-child(1)::before {
  background-color: #ff5151;
}
.day5 .calendar .grafics .index span:nth-child(2)::before {
  background-color: #5566ff;
}

.day6 {
  background-image: linear-gradient(48deg, #ffcb6b 30%, #c57330 90%);
}
.day6 p, .day6 span {
  color: rgba(94, 80, 64, 0.822);
  margin: auto;
  font-family: "Montserrat", sans-serif;
}
.day6 .presentation {
  background-color: rgba(255, 255, 255, 0.95);
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.589);
  height: 25rem;
  width: 95%;
  margin: 1rem auto;
}
.day6 .presentation .presentationMe .presentationBoton {
  margin: 1rem auto;
  border-radius: 2rem;
  border: 1px solid rgba(94, 80, 64, 0.9);
}
.day6 .presentation .presentationMe .presentationBoton p {
  color: #5e5040;
  text-align: center;
  font-size: 1.3rem;
  font-weight: 400;
  margin: 0;
}
.day6 .presentation .count {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
  width: 40%;
  gap: 0.1rem;
}
.day6 .presentation .count .presentationCount {
  padding: 1rem;
  text-align: center;
  height: 100%;
  background-color: rgba(214, 199, 184, 0.555);
  transition: 1s;
}
.day6 .presentation .count .presentationCount:hover {
  background-color: rgba(165, 133, 103, 0.534);
  transition: 1s;
}
.day6 .presentation .count .presentationCount p {
  font-weight: bolder;
  margin-top: 2rem;
  line-height: 1rem;
}
.day6 .presentation .count .presentationCount span {
  font-size: 1.3rem;
}

.dark {
  background-color: #e3fffb;
  transition: 1.5s;
}
.dark .header, .dark .nav {
  background-color: #2AB79B;
}
.dark .header {
  box-shadow: 0px 4px 20px 0px rgba(32, 32, 32, 0.8);
}
@media (max-width: 1000px) {
  .dark .nav {
    border-top: solid 5px #00735c;
  }
}
.dark .section {
  box-shadow: inset -5px 4px 20px 0px #00826140;
}
.dark .glass {
  background-color: #00735c;
  box-shadow: 0px 0px 20px 5px #2e2e2e40;
}
.dark .tools .glass {
  background-color: #e3fffb;
}
.dark .tools .icoinfo .imgaws {
  background-color: unset;
}
.dark .sectionProyect .glass {
  background-color: #e3fffb;
}
.dark .sectionProyect .glass p {
  color: black;
}
.dark .footerAcc {
  background-color: #00735c;
}
.dark .footer {
  background-color: #2AB79B;
  width: 100%;
  max-width: none;
}
.dark .footer .social i {
  text-shadow: none;
}
.dark .info h2 {
  text-shadow: 0 0 5px #005c4c;
}
.dark .important {
  text-shadow: none;
  background-color: azure;
}
.dark .fondoAM {
  background-color: #2ab79bba;
  box-shadow: inset 0 0 20px 5px #3939399c;
}
.dark .arrow {
  box-shadow: 0px 7px 20px #2323237a;
}
.dark .infoHome2 i {
  color: #00735c;
  transition: 1s;
}
.dark .infoHome2 p {
  color: #666666;
  font-weight: bolder;
}
.dark .info2 p {
  text-shadow: 0 0 20px #005c4c;
}
.dark .info2 .btnInfo .btn {
  background-color: #2cac9662;
  border: 2px solid #00745d;
}
.dark .info2 .btnInfo .btn::before {
  background: #00735c;
}
.dark .aux .ContactSocial {
  background-color: #333333;
}
.dark .aux .glass {
  background-color: #333333;
}

